.area {
  grid-column: 1;
  grid-row: 2;

  width: 75%; height: 100%;
  min-width: 310px;

  display: grid;
  
  align-self: center;
  justify-self: end;

  align-items: start;

  overflow-x: hidden;
  overflow-y: auto;

  z-index: 0;
}
 .feed {
   width: 65%; height: auto;
   min-width: 300px;
   min-height: 250px;

   background-color: #FFF;
   border-radius: 2px;
   box-shadow: 0 0 2.5px rgba(235, 235, 235, 0.6);

   margin: 15px 0;
 }
