.blank {
  width: 100%; height: 100%;
  background: rgba(200, 200, 200, 0.1);

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 100%;
}

.blank>div {
  grid-column: 1;
  grid-row: 1;

  min-width: 320px; min-height: 420px;
  max-height: 420px;
  background: #FFF;

  border-radius: 10px;
  box-shadow: 0 0 10px rgba(200, 200, 200, 0.3);

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 50% 20% 30%;

  align-items: center;
  justify-items: center;

  align-self: center;
  justify-self: center;
}

.form {
  grid-column: 1;
  grid-row: 1;

  width: 80%; height: 90%;

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 50% 50%;

  align-self: end;
  justify-self: center;
}
 .inp {
   width: 85%; height: 65%;

   background: rgba(200, 200, 200, 0.25);
   border: none;
   border-radius: 10px;

   font-family: 'Baloo Da 2';
   font-weight: 500;
   font-size: 17px;
   color: #555;

   padding: 0 15px;

   align-self: center;
   justify-self: center;
 }

.buttons {
  width: 100%; height: 100%;

  align-items: center;
  justify-items: start;

  display: grid;

  grid-template-columns: 50% 50%;
  grid-template-rows: 100%;
}

 .join {
   width: 90px; height: 45px;

   background: none;
   border: none;
   border-radius: 0 2px 0 0;
   border-bottom: 2px solid rgba(200, 200, 200, 0.6);
   
   transition: background-color .75s;
   text-align: right;
   padding: 0 15px 0 0;

   align-self: center;
   justify-self: start;
 }
 .join:hover {
   background: rgba(235, 235, 235, 0.3);
   
   transition: background-color .75s;
 }
  .join>a {
    font-family: 'Concert One';
    font-weight: 600;
    font-size: 20.5px;

    letter-spacing: 1px;
    color: #353535;
  }

 .LOGIN {
   width: 90px; height: 50px;

   background: rgba(245, 29, 51, 0.85);
   border: none;
   border-radius: 10px;
   box-shadow: 0 0 4px rgba(245, 29, 51, 1);

   justify-self: center;
 }

 .LOGIN:active {
  transform: scale(0.9);
  transition: .1s;
}
 .LOGIN>span {
   font-family: 'Concert One';
   font-weight: 1000;
   font-size: 18px;

   letter-spacing: 3.5px;
   color: #FFF;
 }

.txtJoinLink { color: #363636; font-size: 25px; transition: .5s; }
.txtJoinLink:active { font-size: 20px; transition: .5s;}

.forgot {
  background: none;
  border: none;

  font-size: 16px;

  align-self: center;
  justify-self: center;
}
 .forgot>span {
   font-family: 'Concert One';
   font-weight: 100;
   font-size: 20px;
  
   letter-spacing: 2px;
   color: #505050;

   transition-property: color;
   transition-duration: .3s;
 }
 .forgot>span:hover {
   color: #000;

   transition-property: color;
   transition-duration: .3s;
 }

@media (max-width: 320px) {

  .blank { background: #FFF; }

  .blank>div {
    box-shadow: none;
    border-radius: none;
  }

}
