.area {
  width: 35%; height: 10%;

  background: #FFF;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(50, 50, 50, 0.1);

  display: grid;

  grid-template-columns: 65% 35%;
  grid-template-rows: 100%;

  align-self: center;
  justify-self: center;
}

 input[type="file"] {
    display: none;
 }

 .label {
    width: 100%; height: 100%;
    display: grid;
 }
  .selectfile {
    width: 200px; height: 100%;
    background: none;
    border: none;
    border-radius: none;
  }
   .select {
     font-family: 'Concert One';
     font-weight: 400;
     font-size: 30px;
     color: #505050;

     transition-property: color;
     transition-duration: .1s;
   }
  .select:hover {
    color: #000;

    transition-property: color;
    transition-duration: .2s;
  }

 .link {
   width: 100%; height: 100%;
   display: grid;
 }

 .upload {
   width: 50%; height: 70%;

   background: rgba(245, 29, 51, .9);
   border: none;
   border-radius: 10px;

   align-self: center;
   justify-self: center;
 }
 .upload:active {
   transform: scale(0.9);

   transition-property: transform;
   transition-duration: 0.1s;
 }
  .uploadInner {
    font-family: 'Concert One';
    font-weight: 400;
    font-size: 22px;
    color: #FFF;
  }
