/* History */

.hs_item {
  width: 80%; height: 100%;
  min-height: 400px;
  max-height: 400px;
  margin: 35px auto 45px auto;

  justify-self: center;
  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 15% 70% 15%;
}
 .hs_header {
   width: 100%; height: 100%;

   display: grid;

   grid-template-columns: 50% 50%;
   grid-template-rows: 100%;
 }
  .hs_author {
    width: 100%; height: 50%;
    
    align-self: center;
    justify-self: center;

    display: grid;
    
    grid-template-columns: 30% 70%;
    grid-template-rows: 100%;

    align-items: center;
    justify-items: center;
  }
   .hs_icon {
     width: 50px;
   }

   .hs_authorName {
     justify-self: start;
     
     display: grid;

     grid-template-columns: auto auto;
     grid-template-rows: 100%;
   }
    .hs_label_author {
      letter-spacing: 2px;
    }

  .hs_kept {
    width: 60px; height: 35px;
    background-color: rgb(225, 225, 225);
    border: none;
    border-radius: 5px;

    align-self: center;
    justify-self: end;

    margin: 0 60px 0 0;
  }
   .hs_kept>span {
     font-family: 'Concert One';
     font-weight: 200;
     font-size: 14px;

     color: #666;

     letter-spacing: 2px;
   }

 .hs_content {

 }

 .hs_footer {
   width: 80%; height: 100%;
   justify-self: center;

   display: flex;

   align-items: center;
   justify-content: space-between;
 }
  .hs_like {
    width: 50px; height: 50px;
   
    background: none;
    border: none;
  }
   .hs_like>span {
     font-family: 'Days One';
     font-weight: 600;
     font-size: 62px;
     
     line-height: 32px;
     color: rgba(245, 29, 51, .6);
     transition: color .2s;
   }
   .hs_like>span:active {
    font-size: 72px;
    line-height: 42px;
    color: rgba(245, 29, 51, 1);
    transition: color .2s;
  }

hr {
  width: 80%; height: 2px;
  margin: auto;
  background-color: #eee;
  border: none;
}
/* short mark */

.mark_item {
  width: 80%; height: 300px;
  margin: 35px auto 45px auto;
  
  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 10% 70% 20%;
}
 .mark_header {
   
 }
  .mark_icon {
    width: 50px;
  }

 .mark_content {

 }

 .mark_footer {
   width: 85%; height: 100%;
   
   justify-self: start;
   display: grid;

   grid-template-columns: 30% 70%;
   grid-template-rows: 100%;
 }
  .mark_like {
    width: 30px; height: 30px;

    background: none;
    border: 6.5px solid rgba(255, 238, 0, 0.6);
    border-radius: 6px;
    
    align-self: center;
    justify-self: start;

    transform: rotate(45deg);
    transition: background-color .2s;
  }
  .mark_like:active {
    border: none;
    background: rgb(255, 230, 0);
    transition: background-color .2s;
  }