.friend {
  width: 90%; height: 120px;

  justify-self: center;

  display: grid;

  grid-template-columns: 30% 50% 20%;
  grid-template-rows: 100%;
}
 .icon {
   width: 90px; height: 90px;
   align-self: center;
 }

 .data {
   width: 100%; height: 70%;

   align-self: center;
   display: grid;

   grid-template-areas:
    'a a b'
    'c c c';
 }
 .name {
   grid-area: a;
   align-self: center;
 }
 .pref {
   grid-area: b;
   width: 30px; height: 30px;

   background: rgba(255, 238, 0, 0.3);
   border: none;
   border-radius: 50%;

   font-family:  'Averia Serif Libre';
   font-weight: 700;
   font-size: 20px;
   color: #c3a720;
 }

 .delete {
   grid-column: 3;
   grid-row: 1;

   width: 80px; height: 40px;

   background: none;
   border: none;
   border-radius: 7px;

   transition-property: background-color;
   transition-duration: .5s;

   align-self: center;
   justify-self: center;
 }
 .delete:hover {
   background: rgba(255, 238, 0, 0.15);
   transition-property: background-color;
   transition-duration: .5s;
 }
  .deleteInner {
    font-family: 'Concert One';
    font-weight: 400;
    font-size: 20px;

    text-transform: uppercase;
    color: rgba(245, 29, 51, 1);
  }
