.area {
  width: 320px; height: 320px;
  background: #FFF;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(200, 200, 200, 0.5);

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 50% 50%;

  align-items: center;
  justify-items: center;

  align-self: center;
  justify-self: center;
}

.inp {
  grid-column: 1;
  grid-row: 1;

  width: 65%; height: 45%;

  background: rgba(200, 200, 200, 0.20);
  border: none;
  border-radius: 10px;

  font-family: 'Baloo Da 2';
  font-weight: 500;
  font-size: 18px;
  color: #303030;

  padding: 0 15px;

  align-self: center;
  justify-self: center;
}

.submit {
  grid-column: 1;
  grid-row: 2;

  width: 50%; height: 30%;
  min-width: 130px; min-height: 60px;
  background: rgba(255, 238, 0, 0.7);
  border: none;
  box-shadow: 0 0 2px #ffff66;

  font-weight: 700;
  font-size: 23px;
  color: #c3a720;

  text-transform: uppercase;

  border-radius: 10px;

  display: grid;

  align-items: center;
  justify-items: center;

  transition-property: background-color;
  transition-duration: .5s;
}
 .submit:hover {
   background: rgba(255, 238, 0, 1);

   transition-property: background-color;
   transition-duration: .5s;
 }
 .submit:active {
   background: rgba(255, 238, 0, 1);
   transform: scale(0.9);

   transition-property: transform;
   transition: .1s;
 }
