::selection { background: #ccc; color: #eee;}

.area {
  width: 100%; height: 100%;

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: minmax(80px, 85px) minmax(90%, 99%);
}

.searchFriend {
  width: 100%; height: 100%;
  background: #FFF;

  box-shadow: 0 0 5px rgba(50, 50, 50, 0.2);
  display: grid;

  z-index: 1;
}
 .itemSearchFriend {
   width: 100%; height: 50%;
   min-width: 320px;
   max-width: 600px;

   align-self: center;
   justify-self: center;

   display: grid;

   grid-template-columns: 70% 30%;
   grid-template-rows: 100%;
 }
  .inp {
    width: 80%;
    background: #FFF;
    border: none;
    border-bottom: 1.5px solid #ccc;

    font-family: 'Baloo Da 2';
    font-weight: 600;
    font-size: 16px;

    text-align: left;
    color: #303030;

    padding: 5px 15px;
  }
  .inp:focus {
    border-bottom-color: #aaa;
  }

  .add {
    width: 70px; height: 40px;

    background: #18e8ff;
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 6px #07d2e9;

    transition: transform .1s;
  }
  .add:active {
    transform: scale(0.9);
    transition: transform .1s;
  }
   .add>span {
     font-family: 'Concert One';
     font-weight: 600;
     font-size: 16px;

     letter-spacing: 2.5px;
     color: #b8f8fc;
   }

.friends {
  width: 100%; height: 100%;

  min-width: 320px;
  max-width: 750px;
  
  background: #FFF;

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: minmax(85%, 95%) minmax(85px, 100px);

  align-self: center;
  justify-self: center;

  z-index: 0;
}
 .loader {
   width: 75px; height: 75px;
   margin: 20% 0 0 0;

   align-self: start;
   justify-self: center;
 }

.friendsfeed {
  width: 70%; height: 90%;

  align-self: center;
  justify-self: center;
} 
  .findfriends {
    text-transform: capitalize;

    font-family: 'Concert One';
    font-weight: 400;
    font-size: 25px;

    letter-spacing: 0.5px;

    color: #505050;

    align-self: center;
    justify-self: center;
  }

   .findfriends::selection {
     color: #FFF;
     background: rgba(255, 238, 0, 0.7);
   }

   .find {
     text-transform: uppercase;
     font-family: 'Concert One';
     font-weight: 700;
     letter-spacing: 2px;
     color: #d1b425;

     cursor: pointer;
   }
   .find::selection {
     color: #FFF;
     background: rgba(255, 238, 0, 0.6);
   }
   .a {
     font-family: 'Concert One';
     text-transform: lowercase;
   }
   .a::selection {
     color: #FFF;
     background: rgba(255, 238, 0, 0.6);
   }

.footer {
  width: 100%; height: 100%;

  min-width: 320px;
  max-width: 560px;
  
  align-self: center ;
  justify-self: center;

  display: grid;

  grid-template-columns: 40% 60%;
  grid-template-rows: 100%;

  align-items: center;
  justify-items: center;
}
 .toprofile {
   width: 35px; height: 35px;
   background: rgba(235, 235, 235, 0.95);
   border: none;
   border-radius: 5px;

   transition: transform .1s;
 }
 .toprofile:active {
   transform: scale(0.9);
   transition: transform .1s;
 }
  .toprofile>span {
    font-family: 'Concert One';
    font-weight: 600;
    font-size: 25px;

    line-height: 15px;
    color: #666;
  }

 .friends__amount {
   width: 120px; height: 50px;
   background: rgba(235, 235, 235, 0.35);
   border-radius: 10px;

   display: grid;

   align-items: center;
   justify-items: center;
 }
  .of {
    font-family: 'Averia Serif Libre';
    font-weight: 600;
    font-size: 20px;

    letter-spacing: 1px;
    color: #666;
  }
  .amount {
    font-family: 'Averia Serif Libre';
    font-weight: 600;
    font-size: 20px;

    letter-spacing: 1px;
    color: #444;
  }